<template>
    <v-container>
        {{ qualites }}
    </v-container>
</template>

<script>
    import axios from "axios";

    export default {
        name: "qualite",
        data: function() {
            return {
                details_file: "files/details.json",
                qualites: null
            };
        },

        methods: {
            getQua: function () {
                const self = this;
                axios.get(this.details_file).then(function (result) {
                    self.qualites = result.data.qualites;
                });
            }
        },

        mounted: function() {
            this.getQua();
        }
    }
</script>

